.row{
    margin-left: 20px;
    color: #fff;
}

.posters{
  display: flex;
  padding: 20px;
  overflow-x: scroll;
  overflow-y: hidden;
}

.poster{
    max-height: 250px;
    margin-right: 10px;
    cursor: pointer;
}


.posters::-webkit-scrollbar{
    display: none;
}

.smallPoster{
   max-height: 150px;
   margin-right: 10px;
   cursor: pointer;
}

.poster:hover{
    transform: scale3d(1.1,1.1,1.1);
}
.smallPoster:hover{
    transform: scale3d(1.1,1.1,1.1);
}